.background {
  background-color: #f8f9fa; /* light gray background */
  min-height: 100vh; /* full height */
  display: flex;
  flex-direction: column;
}

.Navbar {
  margin-bottom: 20px;
}

h1 {
  color: #343a40; /* dark gray */
}

p {
  color: #6c757d; /* gray */
}

img {
  margin-right: 10px;
}


html, body {
  height: 100%;
  margin: 0;
}


.landing-page {
  background-color: black;
  height: 100%;
  width: 100%;
}

.intro {
  color: white;
  padding-top: 30vh;
  font-size: 1.5rem;
  margin-left: 15vw;
  margin-right: 15vw;
  font-family: 'Barlow', sans-serif;
  font-weight: medium;
}
.me {
  color: white;
  font-size: 4.4rem;
  font-family: 'Barlow', sans-serif;
}

.navbar {
  background-color: black;
  color: white;
}
.navbar-light .navbar-nav .nav-item .nav-link {
  font-size: 1.5rem;
  color: white;
}

.navbar-light .navbar-toggler-icon {
  background-color: white;
}
.navbar-toggler {
  /* margin-left: auto; */
  background-color: white;
}


.osmo-work {
  background-color: white;
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
}
.graphic-designer {
  font-size: 4rem;
  font-family: 'Barlow', sans-serif;
  text-align: center;
  font-weight: bold;
}
.carousel {
  width: 90%;
  height: auto;
  margin-right: 3vw;
}
.osmo-description {
  margin-left: auto;
  width: 35%;
  /* height: 76.4%; */
  background-color: rgb(227, 217, 217);
  padding: 6vw;
  text-align: center;
  /* transform: translateY(-76.4vh); */
}


.airflow-work {
  width: 100%;
  height: 100%;
  background-color: black;
  display: flex;
  /* justify-content: center;  Center the content horizontally */
  align-items: center; /* Center the content vertically */
}

.airflow-image {
  width: 60%;
  height: auto;
  display: block; 
  margin-left: 3vw; 
}
.airflow-description {
  font-family: 'Barlow', sans-serif;
  color: white;
}
.apache-airflow {
  font-size: 2.5rem;
  font-weight: bold;
  text-align: center;
}

.my-traits {
  width: 100%;
  height: 80vh;
  display:flex;
  justify-content: center;
  align-items: center;
}
.number {
  color: red;
}
.trait-description {
  font-family: 'Barlow', sans-serif;
}


.basd-work {
  background-color: black;
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
}

.basd-designer {
  font-size: 3.3rem;
  font-family: 'Barlow', sans-serif;
  text-align: center;
  color: white;
}
.basd-resume {
  color: white;
}


.my-experiences {
  width: 100%;
  height: 80%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.card {
  height: 45vh;
  text-align: center;
  font-family: 'Barlow', sans-serif;
}
.card-title {
  font-weight: bold;
  color: red;
  font-size: 1.5rem;
}
@media (max-width: 768px) {
  .my-experiences {
      height: auto;
      display: flex;
      justify-content: center;
      align-items: center;
  }

  .card {
      height: auto;
      margin-top: 5vh;
  }
}


.about {
  width: 100%;
  height: 100%;
  background-color: black;
  display: flex;
  /* justify-content: center;  Center the content horizontally */
  align-items: center; /* Center the content vertically */
}
.deer-image {
  width: 60%;
  height: auto;
  display: block; 
  margin-left: 3vw; 
}
.who-i-am {
  font-family: 'Barlow', sans-serif;
  color: white;
}
.my-career {
  font-size: 2.5rem;
  font-weight: bold;
  text-align: center;
}
.my-info {
  margin: 2vw;
}
.contact-me {
  display: flex;
  justify-content: center;
  align-items: center;
  /* margin-left: auto; */
  margin-left: 2vw;
}





#footer {
  padding: 7% 15%;
  text-align: center;
}

.footer-icon {
  color: orange;
}
.footer-icon:hover {
  color: teal;
}